.layout {
  display: flex;
  flex-flow: row;
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 50;
}
.LayoutIcon {
  margin-left: 10px;
}

.navLinkItem {
  display: flex;
  flex-direction:row;
}

nav {
  background: #FF7110;
  height: 60px;
  width: 80%;
  position: fixed;
  box-shadow: 2px 2px 2px #ccc;

  ul {
    background: #FF7110;
    display: flex;
    margin-block-start: 0;
    margin-block-end: 0;
    padding-inline-start: 0;
    height: 100%;
    align-items: center;
    box-shadow: 2px 2px 2px #ccc;

    li {
      list-style-type: none;
      display: flex;
      flex-direction: row;
      font-size: 17px;
      padding: 20px
    }
    a {
      color: white;
      text-decoration: none;
    }
    i {
      margin-right: 5px;
      color: white;
    }
  }
}

@media screen and (max-width: 759px) {
  nav {
    ul {
      height: 100%;
      width: 300px;
      left: -250px;
      position: fixed;
      top: 60px;
      flex-direction: column;
      transition: 300ms ease all;

      &.active {
        left: 0px;
      }

      .LayoutIcon {
        position: fixed;
        left: 0px;
        top: 10px;
      }

      li {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
        flex-direction:row-reverse;
        justify-content: space-between;
      }
      i {
        margin-left: 20px;
        margin-right: 16px;
      }
      a {
        margin-left: 20px;
        margin-right: 16x;
      }
      .navLinkItemText {
        position: absolute;
        right: 0;
        bottom: 0;
        opacity: 0;
      }
    }
  }
}
