//Custom Fonts
@import url("https://fonts.googleapis.com/css?family=Open+Sans&display=swap");

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  font-family: "Open Sans", sans-serif;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
//Login Main Container
.login {
  width: 27em;
  height: 40em;
  min-height: 44em;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  padding-top: 5em;
  position: relative;
  z-index: 99;
}
.container {
  display: flex;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.2);
  border-radius: 4px;
  position: absolute;
  width: 35vw;
  min-width: 30vw;
  height: 100%;
  top: 5%;
  left: 50%;
  z-index: 2;
  padding: 17px 10px;
  transition: transform 200ms ease-in-out;
  transform: translateX(-50%);
}
.right-side {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 80%;
  background-color: #ff7110;
  width: 100%;
  position: absolute;
  right: -16vw;
  border-radius: 6px;
  z-index: 1;
  transition: all 400ms ease-in-out;
  cursor: pointer;
  box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.281);
}
.right {
  right: -16vw;
}
.right:hover {
  right: -17vw;
}
.left {
  right: 16vw;
  justify-content: flex-start;
}
.left:hover {
  right: 17vw;
}
.text {
  font-size: 21px;
  font-weight: 500;
  color: #fff;
  margin-right: 3em;
  margin-left: 3em;
}
